import React from "react";

function ChooseFretsQtyDropDown(view, setView) {
  const items = [
    { label: "5 frets", value: "5" },
    { label: "10 frets", value: "10" },
    { label: "15 frets", value: "15" },
    { label: "18 frets", value: "18" },
  ];

  return (
    <select onChange={(e) => setView(e.currentTarget.value)}>
      {items.map((item) => (
        <option key={item.value} value={item.value}>
          {item.label}
        </option>
      ))}
    </select>
  );
}

export default ChooseFretsQtyDropDown;
